.publicacoes {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin:0 auto;
    gap: 40px;
    .titulo {
        display: flex;
        justify-content: space-between;
        align-items: center;
        h2 {
            color: rgba(0, 0, 0, 0.80);
            font-family: "Faktum", sans-serif;
            font-size: 22px;
            font-weight: 700;
            text-transform: uppercase;
            margin: 0;
        }
        .ver-todas {
            color: #004D8F;
            font-family: "Faktum", sans-serif;
            font-size: 12px;
            font-weight: 500;
            text-transform: uppercase;
            text-decoration: none;
            position: relative;
            &:hover {
                color: var(--cinza-medio-600);
              }
              &::after {
                content: "";
                position: absolute;
                left: 0;
                bottom: -2px; 
                width: 0%;
                height: 2px;
                background-color: currentColor; 
                transition: width 0.3s ease-in-out;
              }

              &:hover::after {
                width: 100%;
              }
        }
    }
    .grid-container{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
        .post-destaque {
            background: #fff;
            padding: 40px;
            box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);

            &:nth-child(1) {
                grid-column: 1;
                grid-row: 1 / span 2;
            }
            
            &:nth-child(2),
            &:nth-child(4) {
                grid-column: 2;
                grid-row: auto;
            }
    
            &:nth-child(3) {
                grid-column: 3;
                grid-row: 1 / span 2;
            }
        }
        .thumbnail {
            display: flex;
            flex-direction: column;
            justify-content: center;
            gap: 30.24px;
            margin: 0 auto;
            .img-thumbnail  {
                width: 100%;
            }
            .categories{
                margin: 0;
                li{
                    background: var(--PRETO-10, rgba(0, 0, 0, 0.10));
                    a{

                        color: var(--cinza-medio-600);
                        font-family: Faktum;
                        font-size: 12px;
                        font-style: normal;
                        font-weight: 500;
                        text-transform: uppercase;
                        background-color: transparent;
                        text-decoration: none;
                    }
                }
            }
            .title{
                margin: 0;
                a{
                    color: var(--color-black);
                    font-family: 'Faktum', sans-serif;
                    font-size: 22px;
                    font-weight: 500;
                    text-transform: uppercase;
                    text-decoration: none;
                    margin: 0;
                    position: relative;
                    display: inline; 
                    background-image: linear-gradient(var(--cinza-medio-600), var(--cinza-medio-600)); 
                    background-size: 0% 1px;
                    background-repeat: no-repeat;
                    background-position: 0 100%;
                    transition: background-size 0.3s ease-in-out;
                    letter-spacing: -0.66px;

                    &:hover {
                        background-size: 100% 1px;
                    }
                }
            }
            .content{
                p{
                    color: var(--PRETO-40, rgba(0, 0, 0, 0.40));
                    font-size: 18px;
                    font-weight: 400;
                    margin: 0;
                }
            }
            .data-publicacao{
                color: var(--PRETO-40, rgba(0, 0, 0, 0.40));
                font-size: 12px;
                font-weight: 400;
                margin: 0;
                text-transform: uppercase;
            }
        }
    }
    @media (max-width: 640px) {
        padding: 0px 30px;
        gap: 20px;
        .titulo{
            h2{
                font-size: 18px !important;
            }
            a{
                display: none;
            }
        }
        .grid-container{
            display: flex;
            flex-direction: column;
            max-width: 340px;
            .post-destaque{
                padding: 20px;
            }
            .thumbnail{
                gap: 20px;
                .title{
                    a{
                        font-size: 18px;
                        width: 100%;
                    }
                }
                .content{
                    p{
                        font-size: 14px;
                    }
                }
            }
        }
    }
}

.cta-container {
    display: flex;
    justify-content: start; 
    align-items: center; 
    width: 100%;
    max-width: 873px; 
    .cta-link {
        display: flex;
        width: 100%;
        max-width: 100%;
    }
    .cta-image {
        width: 100%;
        height: auto;
        max-width: 100%;
        object-fit: contain;
    }
    @media (max-width: 640px) {
        width: 100%;
        height: 200px;
        margin: 0 auto;
        .cta-image {
            width: 90%;
        }
    }
}

.cta-container-mapa {
    display: flex;
    justify-content: center; 
    align-items: center; 
    width: 100%;
    margin: 0 auto;
    text-align: center; 
    @media (max-width: 768px) {
        height: 441px;
        align-self: stretch;
    }
}

.revista-container{
