        display: flex;
        flex-direction: column;
        gap: 29.34px;
        width: 187.422px;
        h3{
            color: var(--color-black);
            font-family: "Faktum", sans-serif;
            font-size: 28.834px;
            font-weight: 700;
            line-height: normal;
            letter-spacing: -0.865px;
            text-transform: uppercase;
            margin: 0;
        }
    }
    .size-revista{
