/* Estilização da seção */
.colunas-e-resenhas {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin:0 auto;
    gap: 40px;

    .section-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .section-title{
            color: var(--color-black);
            font-family: Faktum;
            font-size: 28.834px;
            font-weight: 700;
            line-height: normal;
            letter-spacing: -0.865px;
            text-transform: uppercase;
            margin: 0;
        }
        .ver-todas {
            color: #004D8F;
            font-family: "Faktum", sans-serif;
            font-size: 12px;
            font-weight: 500;
            text-transform: uppercase;
            text-decoration: none;
            position: relative;
            &:hover {
                color: var(--cinza-medio-600);
              }
              &::after {
                content: "";
                position: absolute;
                left: 0;
                bottom: -2px; 
                width: 0%;
                height: 2px;
                background-color: currentColor; 
                transition: width 0.3s ease-in-out;
              }

              &:hover::after {
                width: 100%;
              }
        }
    }

    .resenhas-container{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 40px;
        .resenha-item {
            background: #fff;
            padding: 30px;
            border-radius: 10px;
            box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
            text-align: center;
        }
    }
}




/* Imagem do livro */
.resenha-thumbnail img {
    width: 100%;
    max-width: 150px;
    height: auto;
    margin-bottom: 20px;
}

/* Título do livro */
.resenha-title {
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 5px;
}

/* Nome do autor */
.resenha-autor {
    font-size: 14px;
    color: #004D8F;
    font-weight: 500;
    text-transform: uppercase;
    margin-bottom: 15px;
}

/* Seção de autor */
.autor-info {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    margin-bottom: 15px;
}

/* Foto do autor */
.autor-foto {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

/* Nome do autor */
.autor-nome {
    font-size: 14px;
    font-weight: 500;
}

/* Citação */
.resenha-citacao {
    font-size: 16px;
    font-style: italic;
    color: rgba(0, 0, 0, 0.7);
    line-height: 1.5;
    margin-bottom: 15px;
}

/* Aspas na citação */
.aspas {
    font-size: 24px;
    color: rgba(0, 0, 0, 0.5);
}

/* Data */
.resenha-data {
    font-size: 12px;
    color: rgba(0, 0, 0, 0.5);
    text-transform: uppercase;
}

/* Responsividade */
@media (max-width: 1024px) {
    .resenhas-container {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 768px) {
    .resenhas-container {
        grid-template-columns: 1fr;
    }

    .section-header {
        flex-direction: column;
        align-items: flex-start;
    }

    .ver-todas {
        margin-top: 10px;
    }
}
